@import url(https://fonts.googleapis.com/css2?family=Merienda&display=swap);
.header{
   background-color: rgb(231,229,232);

}
.span{
    text-decoration: none;
}
.Logo{
    color: white;
    text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
    font-size:3rem;
    margin-right:20px;
}
.Logo:hover{
    text-decoration:none;
    color:white;
}
.custom_nav{
    display:flex;
    justify-content: end;
}
.links{
    color:black;
}
.links:hover{
    text-decoration: none;
    color:black;
    font-size: 1.5rem;
}

.card{
  width:300px;
  margin-bottom:40px;
  height:auto;
  min-height: 350px;
  border: 2px solid black !important;
  border-radius: 10px !important;
}


.card img{
  width:85%;
  height:150px;
  margin-top:25px;
  margin-bottom:15px;
}
.card h3{
  text-align: center;
  font-weight: bolder;
  text-decoration: none;
  color:black;
}

.card p{
  font-weight: lighter;
  color:grey;
  margin-right: auto;
  margin-left: auto;
  font-size:18px;
}
#click{
  font-weight: bold;
}
.Music_Container{
  background: url(/wooodnote/static/media/Home_Background.d6b13229.jpg) no-repeat center center fixed;
  background-size: cover;
  height:100%;
  min-height: 100vh;
}
.Music_Container{
  display:flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.Home h1{
  font-size: 4.5rem;
   padding-top:10%;
   text-align: center;
   color: rgb(31,86,102);
   animation-name: fade;
  animation-duration: 2s;
}
.Home p{
  color: rgb(31,86,102);
  text-align: center !important;
  animation-name: fade;
  animation-duration: 2s;
}
.Home{
  background: url(/wooodnote/static/media/Home_Background.d6b13229.jpg) no-repeat center center fixed;
  background-size: cover;
  height:93vh;
}
#featured{
  display:flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.Home button{
  display:block;
  margin-left:auto;
  margin-right:auto;
}
.Home button a{
  text-decoration: none;
  color:black;
}
#category{
  display:block;
  scroll-behavior: smooth;
  text-align: center;
  padding-top:5%;
  color:white;
  margin-bottom:6%;
  font-size:3rem;
}
.features{
  background: url(/wooodnote/static/media/pexels-asad-photo-maldives.7c2244a2.jpg) no-repeat center center fixed;
  background-size: cover;
}

.Coding_Container{
  background: url(/wooodnote/static/media/Home_Background.d6b13229.jpg) no-repeat center center fixed;
